@import url(https://fonts.googleapis.com/css?family=Audiowide|Open+Sans&display=swap);
html,body{margin:0;padding:0;height:100%}*{box-sizing:border-box}body{background:#d3f9f5 0%;background:-webkit-gradient(linear, left bottom, left top, from(#d3f9f5),to(#fff));background:linear-gradient(to top, #d3f9f5 0%,#fff 100%);background-repeat:no-repeat;background-attachment:fixed;font-family:sans-serif}#app{height:100%;display:flex;justify-content:center;align-items:center}#calculator{background-color:#e6e6e6;min-width:314px;width:314px;display:grid;grid-gap:5px;padding:5px;grid-template-areas:"header" "output" "input-area";grid-template-rows:auto auto 275px;box-shadow:0 6px 6px -3px rgba(0,0,0,0.2),0 10px 14px 1px rgba(0,0,0,0.14),0 4px 18px 3px rgba(0,0,0,0.12)}.history-button{text-decoration:none;background-color:Transparent;border:none;outline:none;cursor:pointer;padding:5px;border:0.5px solid transparent}.history-button:hover{background-color:rgba(119,118,118,0.15);border:0.5px solid #848484}

.header{grid-area:header;color:black;display:flex;justify-content:flex-end}

.display{grid-area:output;color:black;display:flex;flex-direction:column;border-bottom:1px solid black}.display .formula{overflow-y:auto}.display>div{font-size:25px;font-weight:bold;letter-spacing:2px;display:flex;justify-content:flex-end;word-break:break-all;text-align:right;line-height:30px;min-height:34px;max-height:68px;padding:2px}

.history{grid-area:input-area;display:flex;flex-direction:column;height:100%;background-color:#f2f2f4}.history-body{display:flex;flex:1 1;flex-direction:column;overflow:auto;justify-content:flex-start}.history-body h5{margin-top:5px;margin-bottom:0;margin-left:5px}.history-item{font-size:20px;letter-spacing:2px;display:flex;flex-direction:column;align-items:flex-end;flex-shrink:0;word-break:break-all;text-align:right;margin-top:5px;margin-bottom:5px;padding:8px}.history-item:hover{background-color:lightgray}.history-item .history-item-result{font-weight:bold}

.history-footer{display:flex;justify-content:flex-end;align-items:center}

.buttons{grid-area:input-area;display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(5, 1fr);grid-gap:0.2rem}#zero{grid-area:5 / 1 / span 1 / span 2}#decimal{grid-area:5 / 3 / span 1 / span 1}#equals{grid-area:4 / 4 / span 2 / span 1}#add{grid-area:2 / 4 / span 2 / span 1}#subtract{grid-area:1 / 4 / span 1 / span 1}.btn{text-decoration:none;border:0.5px solid #848484;outline:none;cursor:pointer;background-color:#f2f2f4;font-family:"Audiowide",cursive;font-size:16px;font-weight:bold;letter-spacing:normal}.btn:hover{background-color:#afafaf}.btn:active{background-color:#bebebe}.clear-all,.decimal{background-color:#d9d9df}.digit{text-decoration:none;border:0.5px solid #848484;outline:none;cursor:pointer;background-color:#f2f2f4}.operation{background-color:#d9d9df}.operation:hover{background-color:#40b3d2;border-color:#4592a8}.operation:active{background-color:#baf0ff;color:#fff}

